<template>
  <div id="app">
    <div id="nav">
      <b-card toggleable>

        <b-navbar-toggle target="navbar-toggle-collapse">
          <template v-slot:default="{ expanded }">
            <b-icon v-if="expanded" icon="chevron-bar-up"/>
            <b-icon v-else icon="chevron-bar-down"/>
          </template>
        </b-navbar-toggle>

        <b-collapse id="navbar-toggle-collapse" is-nav>
          <b-nav class="ml-auto">
            <b-nav-item to="/">
              {{ $t('nav.home') }}
            </b-nav-item>
            <b-nav-item to="/Contact" disabled>
              {{ $t('nav.contact') }}
            </b-nav-item>
            <b-nav-item to="/News">
              {{ $t('nav.news') }}
            </b-nav-item>

            <b-nav-item>
              <b-button variant="outline-dark" v-for="entry in languages" :key="entry.title" @click="changeLocale(entry.language)">
                <flag :iso="entry.flag" :squared=false /> {{entry.title}}
              </b-button>
            </b-nav-item>
          </b-nav>
        </b-collapse>
      </b-card>
    </div>
    <router-view id="content"/>
  </div>
</template>

<script>
import i18n from './i18n'
import { getUserLocale } from 'get-user-locale'

export default {
  name: 'app',
  data () {
    return {
      languages: [
        {
          flag: 'de',
          language: 'de',
          title: 'DE'
        },
        {
          flag: 'us',
          language: 'en',
          title: 'EN'
        }
      ]
    }
  },
  methods: {
    changeLocale: function (locale) {
      i18n.locale = locale
    }
  },
  mounted () {
    i18n.locale = getUserLocale()
  }
}
</script>

